@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400&display=swap);
body {
  display: flex;
  justify-content: center;
  background: linear-gradient(90deg, rgb(158, 193, 156, 0.8), rgb(171, 211, 212, 0.8));
}

::-webkit-scrollbar { 
  width: 8px;
  opacity: 1;
}
::-webkit-scrollbar-thumb {
  width: 8px; 
  background-color: rgba(224, 224, 224, 0.829); 
  border-radius: 4px;
} 
#app {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 300;
  position: relative;
  overflow: hidden;
  width: 334px;
  height: 600px;
  border: 1px #979797 solid;
  background-color: rgb(236, 233, 233, 0.75);
  border-radius: 4px;
  box-shadow: 0px 0px 20px 7px rgba(0,0,0,0.3);  
}

#screen {
  width: 320px;
  height: 183px;
}

#switcher-btn {
  border: none;
  cursor: pointer;
  outline: none;
  width: 100%;
  position: relative;
  background-color: #c0c0c0;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  color: #333333;
  font-size: 22px;
  font-weight: bold;
  transition: 400ms;
}

#switcher-btn:hover {
  transition: 300ms;
  background-color: #b6b3b3;
}

#switcher-btn-wrap {
  display: flex;
  justify-content: center;
  height: 28px;
  margin-bottom: 7px;
}
#output-container {
  margin-right: 5px;
  overflow: hidden;
  white-space: nowrap;  
}

#log {
  color: #585858;
  font-size: 21px;
  height: 27px;
}

#result {
  line-height: 10vh;
}
.intermediate-res {
  color: rgb(167, 167, 167);
  /* opacity: 0.5; */
}

.output-elem-wrap {
  border-radius: 2px;
  display: flex;
  justify-content: flex-end;
}

.oversized {
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.33) 0%, rgba(236,233,233, 0) 10%);
}

.log-margin-bottom {
  margin-bottom: 35px;
}
#keyboard {
  width: 326px;
  height: 408px;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 4px;
}

#keys-row {
  display: flex;
}
.key, .double-key {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  width: 80px;
  height: 80px;
  background-color: #b6b3b3;
  margin-right: 2px;
  margin-bottom: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 48px;
  border-radius: 3px;
}
.key:active {
  background-color: #8b8b8b;
}

.math-operator {
  background-color: #ff8c00;
  color: #F1F1F1;
}
.math-operator:active {
  background-color: #ffac5e;
}

.double-key {
  width: 162px;
}

.delete {
  font-size: 24px;
}
#journal-container {
  position: absolute;
  height: 413px;
  width: 334px;
  background-color: #333333;
  opacity: 0.99;
  z-index: 999;
  border-bottom-right-radius: 3px;
  border-bottom-left-radius: 3px;
  color: #EBEBEB;
  font-size: 26px;
  transition: 650ms;
}

#journal {
  max-height: 100%;
  overflow-y: auto;
  word-wrap: break-word;
  padding: 0 10px;
}
